<template>
    <div class="zov-month-table">
        <div
            :class="[
                'zov-month-table-cell',
                {
                    'zov-month-table-cell-current': item.years === Number(currentYear)
                }
            ]"
            :disabled="item.disabled"
            v-for="(item, index) in years"
            :key="index + ''"
        >
            <span>
                {{ item.years }}月
            </span>
        </div>
    </div>
</template>
<script>
import dayjs from 'dayjs'
let prefix = 'zov-year-table'
export default {
    name: prefix,
    props: {
        index: {
            // 针对当前年份的差值，供date-picker-header前后切换传值进来
            type: Number,
            default: 0
        }
    },
    data () {
        return {
            currentYear: dayjs().format('YYYY')
        }
    },
    computed: {
        years () {
            let _this = this
            let arr = []
            let day = dayjs()
            for (let i = 0; i < 12; i++) {
                let year = day.subtract(i - _this.index * 12, 'year')
                arr.unshift(Object.assign(year.toObject(), {
                    month: year.format('M')
                }))
            }
            return arr
        }
    }
}
</script>
