<template>
    <div class="zov-day-table">
        <div class="zov-date-picker-header">
            <div class="zov-date-picker-header-btns">
                <span>
                    <Icon iconname="arrow-back"/>
                    <Icon iconname="arrow-back"/>
                </span>
                <Icon iconname="arrow-back"/>
            </div>
            <div class="zov-date-picker-header-center">
                <span>
                    {{ '2019' }}年
                </span>
                    <span>
                    {{ '12' }}月
                </span>
            </div>
            <div class="zov-date-picker-header-btns">
                <Icon iconname="arrow-forward"/>
                <span>
                    <Icon iconname="arrow-forward"/>
                    <Icon iconname="arrow-forward"/>
                </span>
            </div>
        </div>
        <span class="zov-day-table-header">
            <span
                v-for="(item, index) in daysHeader"
                :key="index + ''"
            >
                {{ item }}
            </span>
        </span>
        <div class="zov-day-table-body">
            <template v-for="(item, index) in weeks">
                <div :key="index + ''">
                     <span
                         :class="[
                            'zov-day-table-cell',
                            {
                                'zov-day-table-cell-current': innerItem.date === today.date
                            }
                         ]"
                         :disabled="innerItem.disabled"
                         v-for="(innerItem, innerIndex) in item"
                         :key="innerIndex + ''"
                     >
                        {{ innerItem.date }}
                    </span>
                </div>
            </template>
        </div>
    </div>
</template>
<script>
import dayjs from 'dayjs'
let prefix = 'zov-day-table'
export default {
    name: prefix,
    props: {
        showWeekNumbers: {
            type: Boolean,
            default: false
        }
    },
    data () {
        return {
            today: dayjs().toObject(),
            // 针对当前月份的差值，供date-picker-header前后切换传值进来
            monthIndex: 1,
            // 针对当前年份的差值，供date-picker-header前后切换传值进来
            yearIndex: 0
        }
    },
    computed: {
        daysHeader () {
            return ['日', '一', '二', '三', '四', '五', '六']
        },
        days () {
            let _this = this
            let arr = []
            let day = dayjs()
            let startDay = day.startOf('month')
            let endDay = day.endOf('month')
            let weekLength = startDay.format('d')
            for (let j = 0; j < weekLength; j++) {
                let day = startDay.subtract(j + 1, 'day')
                arr.unshift(Object.assign(day.toObject(), {
                    disabled: true,
                    month: day.format('M')
                }))
            }
            for (let i = 0; i < day.add(_this.monthIndex, 'month').daysInMonth(); i++) {
                let day = startDay.add(i, 'day')
                arr.push(Object.assign(day.toObject(), {
                    disabled: false,
                    month: day.format('M')
                }))
            }
            for (let k = 0; k < 7 - weekLength; k++) {
                let day = endDay.add(k + 1, 'day')
                arr.push(Object.assign(day.toObject(), {
                    disabled: true,
                    month: day.format('M')
                }))
            }
            return arr
        },
        weeks () {
            let arr = []
            let num = Math.ceil(this.days.length / 7)
            for (let i = 0; i < num; i++) {
                arr.push(this.days.slice(i * 7, i * 7 + 7))
            }
            return arr
        }
    }
}
</script>
