<template>
    <div class="zov-month-table">
        <div
            :class="[
                'zov-month-table-cell',
                {
                    'zov-month-table-cell-current': item.month === Number(currentNonth)
                }
            ]"
            :disabled="item.disabled"
            v-for="(item, index) in months"
            :key="index + ''"
        >
            <span>
                {{ item.month }}月
            </span>
        </div>
    </div>
</template>
<script>
import dayjs from 'dayjs'
let prefix = 'zov-month-table'
export default {
    name: prefix,
    props: {
        index: {
            // 针对当前年份的差值，供date-picker-header前后切换传值进来
            type: Number,
            default: 0
        }
    },
    data () {
        return {
            currentNonth: dayjs().format('M')
        }
    },
    computed: {
        months () {
            let arr = []
            for (let i = 1; i <= 12; i++) {
                arr.push({
                    month: i
                })
            }
            return arr
        }
    }
}
</script>
